
.grids {
  position: relative;
  overflow: hidden;
  display: flex;
}
.grids:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 0.013333rem;
  border-top: 1px solid #e8e9eb;
  color: #e8e9eb;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.grids:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 0.013333rem;
  bottom: 0;
  border-left: 1px solid #e8e9eb;
  color: #e8e9eb;
  transform-origin: 0 0;
  transform: scaleX(0.5);
}
.grid {
  flex: 1;
  flex-direction: column;
  justify-content: center;
  position: relative;
  padding: 0.453333rem 0;
}
.grid:before {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 0.013333rem;
  bottom: 0;
  border-right: 1px solid #e8e9eb;
  color: #e8e9eb;
  transform-origin: 100% 0;
  transform: scaleX(0.5);
}
.grid:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 0.013333rem;
  border-bottom: 1px solid #e8e9eb;
  color: #e8e9eb;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.grid:last-child:before {
  display: none;
}
.grid-icon {
  text-align: center;
  font-size: 0;
}
.grid-icon img {
  display: inline-block;
  width: 1.2rem;
  height: 1.2rem;
}
.grid-icon + .grid-label {
  margin-top: 0.266667rem;
}
.grid-label {
  display: block;
  text-align: center;
  color: #999;
  font-size: 0.32rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}


