
.header {
  background-color: #fff;
  width: 100%;
  padding-top: 1.066667rem;
  padding-bottom: 0.933333rem;
}
.user-name {
  color: #333;
  text-align: center;
  margin-top: 0.44rem;
  line-height: 1;
}
[data-dpr="1"] .user-name {
  font-size: 16px;
}
[data-dpr="2"] .user-name {
  font-size: 32px;
}
[data-dpr="3"] .user-name {
  font-size: 48px;
}
.greeting {
  margin-top: 0.266667rem;
  color: #999;
  text-align: center;
}
[data-dpr="1"] .greeting {
  font-size: 13px;
}
[data-dpr="2"] .greeting {
  font-size: 26px;
}
[data-dpr="3"] .greeting {
  font-size: 39px;
}
.company {
  margin-top: 0.266667rem;
  background-color: #fff;
}
.company .company-icon {
  width: 0.426667rem;
  height: 0.426667rem;
  margin-right: 0.346667rem;
  vertical-align: text-top;
}
.company .company-selector .select {
  height: 0.373333rem;
  line-height: 0.373333rem;
  padding: 0 0.266667rem 0 0;
  text-align: right;
  direction: rtl;
  color: #00b38a;
}
.grids-middle:after {
  display: none;
}
.grids-middle .grid:before,
.grids-middle .grid:after {
  display: none;
}
.grids-bottom {
  margin-top: 0.266667rem;
  background-color: #fff;
  flex-direction: column;
  flex-flow: row wrap;
  padding: 0 0.4rem;
}
.grids-bottom:before {
  display: none;
}
.grids-bottom .grid {
  display: flex;
  flex: 1 50%;
  flex-direction: row;
  padding: 0.506667rem 0 0.453333rem 0;
}
.grids-bottom .grid:before {
  height: 50%;
  top: 25%;
}
.grids-bottom .grid:nth-child(2n) .grid-icon {
  padding-left: 0.4rem;
}
.grids-bottom .grid:nth-child(2n):before {
  display: none;
}
.grids-bottom .grid .grid-icon {
  text-align: left;
}
.grids-bottom .grid .grid-icon img {
  width: 0.666667rem;
  height: 0.666667rem;
  margin-right: 0.346667rem;
}
.grids-bottom .grid .grid-label {
  flex: 1;
  flex-direction: column;
  margin-top: 0;
  text-align: left;
}
.grids-bottom .grid .grid-label .label {
  display: block;
  line-height: 1;
}
[data-dpr="1"] .grids-bottom .grid .grid-label .label {
  font-size: 14px;
}
[data-dpr="2"] .grids-bottom .grid .grid-label .label {
  font-size: 28px;
}
[data-dpr="3"] .grids-bottom .grid .grid-label .label {
  font-size: 42px;
}
.grids-bottom .grid .grid-label .label-des {
  display: block;
  line-height: 1;
  color: #999;
  margin-top: 0.2rem;
}
[data-dpr="1"] .grids-bottom .grid .grid-label .label-des {
  font-size: 12px;
}
[data-dpr="2"] .grids-bottom .grid .grid-label .label-des {
  font-size: 24px;
}
[data-dpr="3"] .grids-bottom .grid .grid-label .label-des {
  font-size: 36px;
}
.grids-bottom .grid .grid-label .label-des.link {
  color: #00b38a;
}


