
.cell-select {
  padding: 0;
}
.cell-select .select {
  padding-right: 0.4rem;
}
.cell-select .cell-body:after {
  content: " ";
  display: inline-block;
  height: 20;
  width: 20;
  border-width: 0.053333rem 0.053333rem 0 0;
  border-color: #ededed;
  border-style: solid;
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  position: relative;
  top: -0.026667rem;
  position: absolute;
  top: 50%;
  right: 0.4rem;
  margin-top: -0.133333rem;
}
.select {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  border: 0;
  outline: 0;
  background-color: transparent;
  width: 100%;
  font-size: inherit;
  height: 1.333333rem;
  line-height: 1.333333rem;
  position: relative;
  z-index: 1;
  padding-left: 0.4rem;
}
[data-dpr="1"] .select option {
  font-size: 14px;
}
[data-dpr="2"] .select option {
  font-size: 28px;
}
[data-dpr="3"] .select option {
  font-size: 42px;
}
.select.placeholder {
  color: #999 !important;
}
.cell-select-before {
  padding-right: 0.4rem;
}
.cell-select-before .select {
  width: 2.8rem;
  box-sizing: border-box;
}
.cell-select-before .cell-header {
  position: relative;
}
.cell-select-before .cell-header:after {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 0.013333rem;
  bottom: 0;
  border-right: 1px solid #e8e9eb;
  color: #e8e9eb;
  transform-origin: 100% 0;
  transform: scaleX(0.5);
}
.cell-select-before .cell-header:before {
  content: " ";
  display: inline-block;
  height: 0.266667rem;
  width: 0.266667rem;
  border-width: 0.053333rem 0.053333rem 0 0;
  border-color: #ededed;
  border-style: solid;
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  position: relative;
  top: -0.026667rem;
  position: absolute;
  top: 50%;
  right: 0.4rem;
  margin-top: -0.133333rem;
}
.cell-select-before .cell-body {
  padding-left: 0.4rem;
}
.cell-select-before .cell-body:after {
  display: none;
}
.cell-select-after {
  padding-left: 0.4rem;
}
.cell-select-after .select {
  padding-left: 0;
}
.selector-no-padding {
  padding-left: 0;
}
.selector.cell-select {
  padding: 0;
}
.selector.cell-select-after {
  padding-left: 0.2rem;
}
.selector-readonly {
  width: 100%;
}


